.sidebar-icon {
    display: block;
    padding: 15px 22px;
    transition: all 0.3s;
    &.active {
        background-color: #252836;
    }
    &.active .icon {
        background-color: #EA7C69;
    }

    &.active .icon svg {
        fill: #fff
    }
    &:last-child{
        margin-bottom: 0;
    }
    .icon {
        display: block;
        cursor: pointer;
        border-radius: 5px;
        // margin-bottom: 84px;
        padding: 10px;
        svg {
            fill: #EA7C69;
            width: 40px;
            height: 40px;
        }
       
    }
}

