body {
    margin: 0;
    padding: 0;
    font-family: 'Barlow', sans-serif;
    background: #252836;
    user-select: none;
}

::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-track {
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    background: #EA7C69;
    border-radius: 10px;
}

h1, h2, h3, h4, h5, h6, p, a {
    margin: 0;
    padding: 0;
    text-decoration: none;
}

.home-page {
    display: flex;
}