.block {
    background: #1F1D2B;
    border-radius: 8px;
    display: inline-block;
    padding: 26px 68px 26px 26px;
    margin-top: 24px;
    .block-top {
        display: flex;
        margin-bottom: 10px;
        align-items: center;
        justify-content: space-between;
        .percent {
            font-family: Barlow;
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 140%;
            color: #50D1AA;
        }
    }

    .block__title {
        font-family: Barlow;
        font-style: normal;
        font-weight: 600;
        font-size: 34px;
        line-height: 140%;
        color: #FFFFFF;
    }

    .block__subtitle {
        font-family: Barlow;
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 130%;
        color: #ABBBC2;
    }
}