.modal {
    padding: 64px 24px 24px 24px;
    background: #1F1D2B;
    position: fixed;
    right: 0;
    width: 510px;
    height: 100%;
    z-index: 4;

    .modal-header {
        border-bottom: 1px solid #393C49;
        padding-bottom: 24px;
        .header__title {
            font-family: Barlow;
            font-style: normal;
            font-weight: 600;
            font-size: 28px;
            line-height: 140%;
            color: #FFFFFF;
        }

        .header__subtitle {
            font-family: Barlow;
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 140%;
            color: #ABBBC2;
        }
    }

    .form-section {
        margin-top: 24px;
        .form__title {
            font-family: Barlow;
            font-style: normal;
            font-weight: 600;
            font-size: 24px;
            line-height: 140%;
            color: #FFFFFF;
            margin-bottom: 24px;
        }
        .buttons {
            display: flex;
            margin-bottom: 24px;
            button {
                background: #1F1D2B;
                border: 1px solid #393C49;
                box-sizing: border-box;
                border-radius: 8px;
                padding: 12px 20px;
                margin-right: 8px;
                display: block;
                &:first-child {
                    background: #252836;
                    border: 1px solid #ABBBC2;
                }

                p {
                    font-family: Barlow;
                    font-style: normal;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 130%;
                    color: #FFFFFF;
                }
            }
        }

        .cardholder__name {
            font-family: Barlow;
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 130%;
            margin-bottom: 8px;
            color: #FFFFFF;
        }

        .cardholder-name__input {
            background: #2D303E;
            border: 1px solid #393C49;
            box-sizing: border-box;
            border-radius: 8px;
            width: 100%;
            padding: 14px;
            outline: none;
            font-family: Barlow;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 140%;
            color: #E0E6E9;
            margin-bottom: 24px;
        }

        .bottom-form {
            display: flex;
            justify-content: space-between;
            border-bottom: 1px solid #393C49;
            padding-bottom: 24px;
            .left {

                .left__title {
                    font-family: Barlow;
                    font-style: normal;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 130%;
                    margin-bottom: 8px;
                    color: #FFFFFF;
                }
                .left__input {
                    background: #2D303E;
                    border: 1px solid #393C49;
                    box-sizing: border-box;
                    border-radius: 8px;
                    padding: 14px 80px 14px 14px;
                    outline: none;
                    font-family: Barlow;
                    font-style: normal;
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 140%;
                    color: #E0E6E9;
                    margin-bottom: 24px;
                }
            }

            .right {

                .right__title {
                    font-family: Barlow;
                    font-style: normal;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 130%;
                    margin-bottom: 8px;
                    color: #FFFFFF;
                }
                .right__input {
                    background: #2D303E;
                    border: 1px solid #393C49;
                    box-sizing: border-box;
                    border-radius: 8px;
                   
                    padding: 14px 80px 14px 14px;
                    outline: none;
                    font-family: Barlow;
                    font-style: normal;
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 140%;
                    color: #E0E6E9;
                    margin-bottom: 24px;
                }
            }
        }

        .form-bottom {
            display: flex;
            justify-content: space-between;
            margin-top: 24px;
            margin-bottom: 65px;
            .left {

                .left__title {
                    font-family: Barlow;
                    font-style: normal;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 130%;
                    margin-bottom: 8px;
                    color: #FFFFFF;
                }
                .left__input {
                    background: #2D303E;
                    border: 1px solid #393C49;
                    box-sizing: border-box;
                    border-radius: 8px;
                    padding: 14px 150px 14px 14px;
                    outline: none;
                    font-family: Barlow;
                    font-style: normal;
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 140%;
                    color: #E0E6E9;
                    margin-bottom: 24px;
                }
            }

            .right {

                .right__title {
                    font-family: Barlow;
                    font-style: normal;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 130%;
                    margin-bottom: 8px;
                    color: #FFFFFF;
                }
                .right__input {
                    background: #2D303E;
                    border: 1px solid #393C49;
                    box-sizing: border-box;
                    border-radius: 8px;
                   
                    padding: 14px 80px 14px 14px;
                    outline: none;
                    font-family: Barlow;
                    font-style: normal;
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 140%;
                    color: #E0E6E9;
                    margin-bottom: 24px;
                }
            }
        
        }

        .bottom-buttons {
            display: flex;
            justify-content: space-between;
            
            button {
                border: 1px solid #EA7C69;
                box-sizing: border-box;
                border-radius: 8px;
                padding: 14px 65px;
                background-color: #1F1D2B;
                font-family: Barlow;
                font-style: normal;
                font-weight: 600;
                font-size: 14px;
                line-height: 140%;
                color: #EA7C69;
                width: 48%;
                cursor: pointer;


                &:last-child {
                    background: #EA7C69;
                    color: #fff;
                }
            }
            
        }

    }
}