.menu-list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
   .list {
    background: #1F1D2B;
    border-radius: 16px;
    padding: 21px;
    width: 192px;
    position: relative;
    margin-bottom: 65px;
    cursor: pointer;
    // margin-right: 28px;
    img {
        position: absolute;
        top: -40px;
        left: 50px;
    }

    .food__name {
        margin-top: 90px;
        font-family: Barlow;
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 130%;
        text-align: center;
        color: #FFFFFF;
        width: 189px;
        margin-bottom: 8px;
    }

    .food__price {
        font-family: Barlow;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 140%;
        text-align: center;
        color: #FFFFFF;
    }

    .food__subtitle {
        font-family: Barlow;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 140%;
        text-align: center;
        color: #ABBBC2;
    }
   }
    
}