.dashboard {
    margin-top: 24px;
    width: 1100px;
    margin-left: 170px;
    .dashboard-header {
        border-bottom: 1px solid #393C49;
        padding-bottom: 24px;
        .header__title {
            font-family: Barlow;
            font-style: normal;
            font-weight: 600;
            font-size: 28px;
            line-height: 140%;
            color: #FFFFFF;
        }

        .header__subtitle {
            font-family: Barlow;
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 140%;
            color: #ABBBC2;
        }
    }

    .calculation {
        display: flex;
        justify-content: space-between;
        margin-bottom: 24px;
    }

    .dashboard-menu {
        border-radius: 8px;
        width: 100%;
        background-color: #1F1D2B;
        .menu-top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 24px;
            .menu__title {
                font-family: Barlow;
                font-style: normal;
                font-weight: 600;
                font-size: 24px;
                line-height: 140%;
                color: #FFFFFF;
            }
        }
        .menu-bottom {
            margin-top: 24px;
            display: flex;
            justify-content: space-between;
            padding-left: 24px;
            padding-right: 24px;
            padding-bottom: 24px;
            border-bottom: 1px solid #393C49;
            a {
                font-family: Barlow;
                font-style: normal;
                font-weight: 600;
                font-size: 18px;
                line-height: 140%;
                color: #FFFFFF;
            }
        }
    }

    .wrapper{
        height: 400px;
        overflow: scroll;
    }
}