.order-list {
    margin-top: 50px;
    .list {
        display: flex;
        justify-content: space-between;
        .list-left {
          
            .left-top {
                display: flex;
                margin-bottom: 15px;
                img {
                    width: 60px;
                    height: 60px;
                    margin-right: 8px;
                }

                .left-titles {
                    margin-right: 100px;
                    .food__name {
                        display: block;
                        font-family: Barlow;
                        font-style: normal;
                        font-weight: 500;
                        font-size: 18px;
                        line-height: 130%;
                        color: #FFFFFF;
                        margin-bottom: 8px;
                    }
                    .food__price {
                        font-family: Barlow;
                        font-style: normal;
                        font-weight: 500;
                        font-size: 14px;
                        line-height: 140%;
                        color: #ABBBC2;
                    }
                }

                .food__number {
                    font-family: Barlow;
                    font-style: normal;
                    font-weight: 500;
                    font-size: 20px;
                    line-height: 140%;
                    color: #FFFFFF;
                    background: #2D303E;
                    border: 1px solid #393C49;
                    box-sizing: border-box;
                    border-radius: 8px;
                    padding: 14px 21px;
                }
            }
            .left-bottom {
                input {
                    background: #2D303E;
                    border: 1px solid #393C49;
                    box-sizing: border-box;
                    border-radius: 8px;
                    padding: 14px 84px 14px 14px;
                    font-family: Barlow;
                    font-style: normal;
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 140%;
                    color: #E0E6E9;
                    width: 100%;
                    outline: none;
                }
            }
        }

        .list-right {
            .total__price {
                font-family: Barlow;
                font-style: normal;
                font-weight: 500;
                font-size: 20px;
                line-height: 140%;
                text-align: right;
                color: #FFFFFF;
                margin-bottom: 45px;
            }

            img {
                width: 50px;
                height: 50px;
                cursor: pointer;
            }
        }
    }
}