.order {
    padding: 24px;
    background: #1F1D2B;
    position: fixed;
    right: 0;
    width: 510px;
    height: 100%;
    z-index: 5;
    &.hidden{
        display: none;
    }
    .order-header {
        border-bottom: 1px solid #393C49;
        padding-bottom: 25px;
        .header-top {
            margin-bottom: 24px;
            .header-top__title {
                font-family: Barlow;
                font-style: normal;
                font-weight: 600;
                font-size: 24px;
                line-height: 140%;
                color: #ffffff;
            }
        }
        .header-center {
            display: flex;
            margin-bottom: 24px;
            button {
                background: #1F1D2B;
                cursor: pointer;
                padding: 7px 12px;
                font-family: Barlow;
                font-style: normal;
                font-weight: 600;
                font-size: 14px;
                line-height: 140%;
                color: #EA7C69;
                border: 1px solid #393C49;
                box-sizing: border-box;
                border-radius: 8px;
                margin-right: 8px;
                outline: none;
                &:first-child {
                    background: #EA7C69;
                    color: #FFFFFF;
                    border-radius: 8px;
                }
            }
        }

        .header-bottom {
            display: flex;
            justify-content: space-between;
            .bottom-left {
                .left__title {
                    font-family: Barlow;
                    font-style: normal;
                    font-weight: 600;
                    font-size: 20px;
                    line-height: 140%;
                    color: #ffffff;
                }
            }
            .bottom-right {
                display: flex;
                .right__title {
                    font-family: Barlow;
                    font-style: normal;
                    font-weight: 600;
                    font-size: 20px;
                    line-height: 140%;
                    margin-right: 45px;
                    color: #ffffff;
                }
                .right__title2 {
                    font-family: Barlow;
                    font-style: normal;
                    font-weight: 600;
                    font-size: 20px;
                    line-height: 140%;
                    color: #ffffff;
                }
            }
        }
    }

    .orders-section {
        height: 550px;
        overflow-y: scroll;
    }

    .payment-desk {
        border-top: 1px solid #393C49;;
        .discount {
            display: flex;
            justify-content: space-between;
            margin-bottom: 19px;
            h2 {
                font-family: Barlow;
                font-style: normal;
                font-weight: normal;
                font-size: 20px;
                line-height: 140%;
                color: #ABBBC2;
            }
            h3 {
                font-family: Barlow;
                font-style: normal;
                font-weight: 500;
                font-size: 24px;
                line-height: 140%;
                text-align: right;
                color: #FFFFFF;
            }
        }

        .total {
            display: flex;
            justify-content: space-between;
            margin-bottom: 50px;
            h2 {
                font-family: Barlow;
                font-style: normal;
                font-weight: normal;
                font-size: 20px;
                line-height: 140%;
                color: #ABBBC2;
            }
            h3 {
                font-family: Barlow;
                font-style: normal;
                font-weight: 500;
                font-size: 24px;
                line-height: 140%;
                text-align: right;
                color: #FFFFFF;
            }
        }

        button {
            background: #EA7C69;
            box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.3);
            border-radius: 8px;
            font-family: Barlow;
            font-style: normal;
            font-weight: 600;
            font-size: 14px;
            line-height: 140%;
            color: #FAFAFA;
            padding: 14px 116px;
            border: none;
            outline: none;
            width: 100%; 
            cursor: pointer;         
        }
    }
}