.main {
    width: 1100px;
    margin-left: 170px;
    .header {

        .header-top {
            display: flex;
            justify-content: space-between;
            margin-top: 24px;
            margin-bottom: 24px;

            .header-left {
                margin-right: 350px;

                .header__title {
                    font-style: normal;
                    font-weight: 600;
                    font-size: 28px;
                    line-height: 140%;
                    color: #FFFFFF;
                }

                .header__subtitle {
                    font-style: normal;
                    font-weight: normal;
                    font-size: 16px;
                    line-height: 140%;
                    color: #E0E6E9;
                }
            }

            .header-right {
                input {
                    width: 220px;
                    height: 48px;
                    background: #2D303E;
                    border: 1px solid #393C49;
                    box-sizing: border-box;
                    border-radius: 8px;
                    font-style: normal;
                    font-weight: normal;
                    font-size: 14px;
                    line-height: 140%;
                    padding: 14px 24px;
                    color: #ABBBC2;
                    outline: none;
                    position: relative;

                }
            }
        }

        .header-bottom {
            display: block;

            .header-links {
                display: block;
                margin-bottom: 13px;

                .links {
                    font-family: Barlow;
                    font-style: normal;
                    font-weight: 600;
                    font-size: 14px;
                    line-height: 140%;
                    color: #FFFFFF;
                    margin-right: 32px;

                    &:first-child {
                        color: #EA7C69;
                    }
                }
            }

            .header-line {
                align-items: center;
                margin-bottom: 24px;

                .line {
                    width: 100%;
                    height: 1px;
                    position: relative;
                    background-color: rgba(167, 167, 167, 0.397);

                    span {
                        position: absolute;
                        width: 50px;
                        height: 5px;
                        top: -2px;
                        border-radius: 5px;
                        background: #EA7C69;
                    }
                }
            }
        }
    }

    .tablet {
        .tablet-top {
            display: flex;
            justify-content: space-between;
            margin-bottom: 70px;

            .tablet__title {
                font-family: Barlow;
                font-style: normal;
                font-weight: 600;
                font-size: 20px;
                line-height: 140%;
                color: #FFFFFF;
            }

            select {
                padding: 15px 30px;
                left: 532px;
                top: 0px;
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 130%;
                color: #FFFFFF;
                outline: none;
                background: #1F1D2B;
                border: 1px solid #393C49;
                box-sizing: border-box;
                border-radius: 8px;
            }
        }
    }

}