.most-foods {
    display: flex;
    margin-top: 24px;
    
    img {
        width: 40px;
        height: 40px;
        margin-right: 10px;
    }
    .food-titles {
        .title {
            font-family: Barlow;
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 130%;
            color: #E0E6E9;
        }
        .subtitle {
            font-family: Barlow;
            font-style: normal;
            font-weight: normal;
            font-size: 12px;
            line-height: 140%;
            color: #ABBBC2;
        }
    }
}