.sidebar {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 104px;
    background-color: #1F1D2B;
    padding: 24px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    .sidebar-logo {
        margin-bottom: 39px;
    }
}