.contact{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    box-sizing: border-box;

    .img-wrapper{
        display: flex;
        align-items: center;
        width: 200px;

        &__img{
            margin-right: 10px;
        }

        &__name{
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 140%;
            color: #E0E6E9;
        }
    }

    .text-wrapper{
        width: 137px;
        margin-left: -20px;

        &__text{
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 140%;
            color: #E0E6E9;
        }
    }

    .payment{
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 140%;
        color: #E0E6E9;
        width: 180px;
    }

    .status{
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 130%;
        color: #50D1AA;
        padding: 4px 12px;
        background: rgba(107, 226, 190, 0.24);
        border-radius: 30px;
    }
}