.most-ordered {
    position: fixed;
    top: 24px;
    right: 24px;
    padding: 24px;
    background: #1F1D2B;
    border-radius: 8px;
    width: 455px;
    padding-bottom: 24px;
    border-bottom: 1px solid #393C49;
    .most-header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .header__title {
            font-family: Barlow;
            font-style: normal;
            font-weight: 600;
            font-size: 20px;
            line-height: 140%;
            color: #FFFFFF;
        }
        select {
            background: #1F1D2B;
            border: 1px solid #393C49;
            box-sizing: border-box;
            border-radius: 8px;
            padding: 14px;
            color: #FFFFFF;

        }
    }
    .food-list {
        button {
            border: 1px solid #EA7C69;
            box-sizing: border-box;
            border-radius: 8px;
            background-color: #1F1D2B;
            color: #EA7C69;
            width: 100%;
            padding: 16px;
            margin-top: 24px;
        }
    }
}